import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate'
import {getCart} from "../api/cart";
import {SUCCESS} from "../config/base";

Vue.use(Vuex);

//state   数据唯一来源
//mutations 状态修改的唯一方法

let store=new Vuex.Store({
    state:{
        token:'',
        isShow:false,
        cart:null
    },
    //同步
    mutations:{
        changeToken(state,token){
            state.token = token;
        },
        changeUser(state,id){
            state.id = id;
        },
        toggleIsShow(state,bool){
           state.isShow = bool
        },
        setCart(state,cart){
            state.cart = cart
        },
        //分类页的商品数量的累加
        /*  incGoods(state,gid){
            //判断购物车是否存在
            if (!state.cart){
                state.cart = {
                    cid:'',
                    total:1,
                    price:'',
                    uid:'',
                    goods:[]
                }
            }
            let goods = state.cart.goods.filter(ele=>ele.gid==gid)[0];
            //判断购物车中是否存在该商品
            if (goods){
                goods.num++;
            } else {
                state.cart.goods.push({gid:gid,num:1,status:1})
            }
        },*/

        incGoods(state,payload){
          let{cid,gid,gname,gthumb,uid,sale} = payload;
          if (!state.cart){
              state.cart = {
                  cid:cid,
                  uid:uid,
                  total:1,
                  price:sale,
                  goods:[]
              };
          }
            let goods = state.cart.goods.filter(ele=>ele.gid == gid)[0];
          if (goods){
              goods.num++;
          } else {
              state.cart.goods.push({gid:gid,num:1,status:1,gname,gthumb,sale})
          }

      },
        decGoods(state,payload){
            let{gid} = payload;
            if (!state.cart){
                return '购物车不存在';
            }
            let goods = state.cart.goods.filter(ele=>ele.gid == gid)[0];
            if (goods){
                goods.num--
            }
            if (goods.num ==0){
                let index = state.cart.goods.indexOf({gid});
                state.cart.goods.splice(index,1);
            }
        },
        calcTotal(state){
          let total = 0;
          let goods = state.cart.goods.filter(ele=>ele.status == 1);
          if (goods.length){
              total =goods.reduce(function (prev,current) {
                   return prev + current.num
              },0)
          }
          state.cart.price = total;
      },
        calcPrice(state){
            let total = 0;
            let goods = state.cart.goods.filter(ele=>ele.status == 1);
            if (goods.length){
                total = goods.reduce(function (prev,current) {
                     return prev + current.num * current.sale
                },0)
            }
            state.cart.price = total;

        },
        togggleGoodsStatus(state,payload){
            let gid = payload.gid;
            let goods = state.cart.goods.filter(ele=>ele.gid==gid)[0];
            // goods.status  = goods.status?0:1;
            goods.status = (!goods.status)*1;
        },
        allStatus(state,payload){
            let data=state.cart.goods;
            for(let i=0;i<data.length;i++){
                data[i].status=payload.status*1;
            }
            state.cart.goods=data;
        },
        ordersFinish(state){
            state.cart.goods = state.cart.goods.filter(ele=>ele.status == 0);
            if (state.cart.goods.length){
                state.cart.total = 0;
                state.cart.price = 0;
            } else{
                state.cart = null
            }
        }
    },
    //异步
    actions:{
      //在actions中可以提交mutation。
        setCart(context){
            getCart('/api/cart/1').then(res=>{
                if (res.code==SUCCESS && res.data){
                    context.commit('setCart',res.data);
                }
            })
        }
    },
    getters:{
        goodsNumber(state){
            return function(gid){
                let num = 0;
                if (!state.cart){
                    return num;
                }
                let goods = state.cart.goods.filter(ele=>ele.gid==gid)[0];
                if (goods){
                    num = goods.num;
                }
                return num;
            }

        },
        goodsTotal(state){
            return function () {
                let total = 0;
                if (!state.cart){
                    return total;
                }
                let goods = state.cart.goods;
                if (goods){
                    total = goods.length
                }
                return total;

            }
        },
        cartPrice(state){
            if (!state.cart){
                return
            }
            let goods = state.cart.goods.filter(ele=>ele.status);

            let price = 0;
            if (goods.length){
                for (let i = 0;i<goods.length;i++){
                    price += goods[i].num * goods[i].sale;
                }
            }
            return price;
        },
        isFullSelected(state){
            return state.cart && state.cart.goods.every(ele=>ele.status);
        },
        existSelectedGoods(state){
            let flag = false;
            if (state.cart){
                flag = state.cart.goods.some(ele=>ele.status);
            }
            return flag;
        }
    },
    plugins:[createPersistedState()]
});
export {store}
