import Vue from 'vue'
import VueRouter from 'vue-router'
import {store} from '../store/index'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: ()=>import('../views/content/index'),
    meta:{title:'生鲜系统'},
  },
  {
    path:'/list',
    name:'list',
    component:()=>import('../views/content/list')
  },
  {
    path:'/cate',
    name:'cate',
    component:()=>import('../views/content/Cate')
  },
  {
    path:'/detail',
    name:'detail',
    component:()=>import('../views/content/Detail')
  },
    //登录注册
  {
    path:'/login',
    name:'login',
    component:()=>import('../views/info/Login')
  },
  {
    path:'/register',
    name:'register',
    component:()=>import('../views/info/Register')
  },
  {
    path:'/cart',
    name:'cart',
    component:()=>import('../views/content/Cart'),
    meta:{title:'购物车',auth:true}
  },
  {
    path:'/my',
    name:'my',
    component:()=>import('../views/content/my'),
    meta: {title:'个人中心',auth:true}
  },
  {
    path:'/orders',
    name:'orders',
    component:()=>import('../views/content/Orders'),
    meta:{title:'订单页',auth:true}
  },
 {
   path:'/pay',
       name:'pay',
     component:()=>import('../views/content/Pay'),
     meta:{title:'订单页',auth:true}
 }

];

const router = new VueRouter({
  routes
});
router.beforeEach((to,from,next)=>{
  document.title = to.meta.title || '生鲜系统';
  if (to.meta.auth){
    if(store.state.token.trim()){
      next();
    }else {
      next({name:'login',query:{redirect:to.name}})
    }
  } else {
    next();
  }
});
export default router
