import instance from './http'
//添加购物车
function addGoods(url,data) {
    return instance.post(url,data);
}
//购物车减去
function reduceGoods(url,data) {
    return instance.post(url,data)
}
//获取购物车
function getCart(url) {
    return instance.get(url);
}
//改变购物车状态
function toggleGoodsStatus(url,obj) {
    return instance.put(url,obj)
}
//下单
function insertOrders(url) {
    return instance.post(url)
}
//全选 改变所有商品状态
function allStatus(url,data){
    return instance.post(url,data)
}
export {addGoods,getCart,toggleGoodsStatus,reduceGoods,insertOrders,allStatus}
